* {
  box-sizing: border-box;
}

input:focus,
select:focus,
button:focus {
  outline: none;
}

body {
  font-family: 'Noto Sans', sans-serif;
}

h1 {
  margin: 0;
  text-align: center;
  font-size: 1.3rem;
  font-weight: 700;
  color: #333;
}

h2 {
  font-size: 1.1rem;
  font-family: 'Merriweather', serif;
  font-weight: 700;
}

p {
  font-size: .7rem;
  color: #999;
}

a {
  text-decoration: none;
  color: #333;
}

form {
  display: flex;
  align-items: center;
}

input,
input::placeholder {
  padding: 0;
  font-family: 'Merriweather', serif;
  font-size: 1.1rem;
  font-weight: 700;
}

input {
  width: 360px;
  border: none;
}

input::placeholder {
  color: #ddd;
}

main {
  width: 360px;
  margin: 0 auto 0 auto;
  padding: 8px;
  transition: all 0.4s;
}

header {
  width: 344px;
  background-color: rgba(255,255,255, 0.99);
  position: fixed;
  top: 0;
}

form {
  margin-top: 16px;
}

.red-txt {
  color: #376693;
}

.blue-txt {
  color: #CC4F54;
}

.search {
  padding: 0;
  background-color: #FFF;
  border: none;
  color: none;
  text-decoration: none;
  cursor: pointer;
}

.search img {
  padding-top: 8px;
  margin: 0;
  width: 12px;
}

.divider {
  margin-top: 6px;
  height: 2px;
  background-color: #333;
}

.sides {
  margin-top: 16px;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  color: #ddd;
  font-family:
  'Merriweather', serif;
  font-size: .8rem;
  font-weight: 700;
}

.left, .right {
  cursor: pointer;
}

.left:hover {
  color: #376693;
}

.right:hover {
  color: #CC4F54;
}

.source {
  margin-top: 112px;
}

.article {
  padding: 12px 0 12px 0;
  display: flex;
  justify-content: space-between;
}

.article img {
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 4px;
}

.text {
  padding-right: 8px;
  width: 240px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.headline {
  margin: 0;
  display: block;
  height: 80px;
  line-height: 20px;
  overflow: hidden;
  word-wrap: break-word;
}

p.author {
  margin: 0;
}

.date {
  margin: 0;
  font-weight: 700;
}
